import React, { FC } from 'react'
import { PartialPageErrorProps } from './types'
import { PageError } from './pageError'

const DefaultContent: FC = () => <>Oops! Something went wrong.'</>

export const Page500: FC<PartialPageErrorProps> = ({ title, content }) => (
  <PageError title={title ?? '500'} content={content ?? <DefaultContent />} />
)
